.A4 {
  width: calc(210mm - 20mm);
  min-height: calc(297mm - 20mm);
  margin: auto;
  margin-top: 40px !important;
  box-shadow: 0 10px 29px 0 rgb(68 88 144 / 10%);
  background: #fff;
  padding: 10mm;
}

button {
  height: 40px !important;
  font-size: 21px;
  font-weight: 600;
  color: #004da0d8;
  box-shadow: 0 5px 15px 0 rgb(68 88 144 / 10%);
  background: #fff;
  border-radius: 6px;
  cursor: pointer;
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: center;
  padding: 0px 16px;
  border: none;
}

/*----------Table-----------*/
h4 {
  text-align: center;
  margin: 0;
  margin-bottom: 20px;
}

table {
  border-collapse: collapse;
  background-color: #fff;
  width: 100%;
  font-size: 14px;
}

tr {
  border: 1px solid #E2E8F0;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

td,
th {
  padding: 5px 10px;
  height: 35px;
}

table thead tr {
  background: #718096;
  color: #fff;
}

.DirectionName {
  background-color: #E2E8F0;
  border-bottom: 1px solid #718096;
}

.DirectionName th {
  height: 20px;
}

.bestRows {
  background: #D3FCE0;
}

/*----------select-----------*/
.Selectors_Wrapper {
  /* border-bottom: 2px solid #007bff; */
  padding: 16px 40px;
  margin: 0 -40px;
  box-shadow: 0 10px 29px 0 rgb(68 88 144 / 10%);
  background-color: #fff;
  position: sticky;
  top: 0;
  background-image: url("./Components/CardBackgraund.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position-y: center;
  z-index: 99;
  align-items: center;
}

.Select {
  font-size: 12px;
  min-width: 150px;
  width: 100%;
}

.my_select__control {
  box-shadow: 0 5px 15px 0 rgb(68 88 144 / 10%) !important;
  border: 1px solid #C3DAFE !important;
  cursor: pointer !important;
  height: 40px;
}

/* .my_select__value-container {} */

/* .my_select__indicator-separator {
  display: none;
} */

/* .my_select__indicators {} */

.my_select__menu {
  padding: 0 !important;
  box-shadow: 0 5px 15px 0 rgb(68 88 144 / 10%) !important;
  border: 1px solid #C3DAFE;
  margin: 2px !important;
  width: calc(100% - 4px) !important;
  background-color: #F4F9FF !important;
}

.my_select__menu-list {
  padding: 0 !important;
}

.my_select__option {
  cursor: pointer !important;
}

.my_select__option .isSelected {
  background: chartreuse !important;
}

.Scroll_Top_button {
  display: none;
  position: fixed;
  transform: translate(-50%, -50%);
  bottom: 10px;
  right: -10px;
  border-radius: 50%;
  width: 50px !important;
  height: 50px !important;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  z-index: 999999;
  border: none;
  box-shadow: 0px 2px 3px #0b1d3333;
  cursor: pointer;
  background-color: cornflowerblue;
}

.Scroll_Top_button i {
  font-size: 18px;
  color: #fff;
}

@media only screen and (max-width: 980px) {
  .Rep_List_Wrapper {
    grid-template-columns: 1fr;
  }
}