    position: absolute;
    top: 40px;
    right: 40px;
} */

.Report4_Wrapper .A4 {
    orientation: landscape;
    width: calc(297mm - 20mm);
    min-height: calc(210mm - 20mm);
    margin: auto;
    margin-top: 40px !important;
    box-shadow: 0 10px 29px 0 rgb(68 88 144 / 10%);
    background: #fff;
    padding: 10mm;
}


.Report4_Wrapper thead tr {
    background: linear-gradient(90deg, #A1C4FD 0%, #C2E9FB 100%);
}

.Report4_Wrapper thead th {
    color: #3f3f3f;
}

.yesNo div {
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: -5px -10px;
    height: 100%;
}

.yesNo div:hover::before {
    font-family: "Font Awesome 5 Free";
    font-weight: 600;
    content: "\f35d";
    color: #1545B3;
    position: absolute;
    padding: 10px;
    background-color: #fff;
    cursor: pointer;
}

.yesNo .fa-plus {
    color: #3f3f3f;
}

/* .Report4_Wrapper .fa-plus:hover {
    background: linear-gradient(90deg, #A1C4FD 0%, #C2E9FB 100%);
} */

.yesNo .fa-minus {
    color: #aaa;
}

.Report4_Wrapper .Documents_Name {
    padding: 6px 3px;
    vertical-align: bottom;
    position: relative;
    height: 250px;
    font-size: 12px;
    vertical-align: middle;
    writing-mode: vertical-rl;
    -webkit-writing-mode: vertical-rl;
    -ms-writing-mode: vertical-rl;
}

.Report4_Wrapper .Documents_Name p {
    transform: rotate(-180deg);
    -webkit-transform: rotate(-180deg);
    -moz-transform: rotate(-180deg);
    -ms-transform: rotate(-180deg);
    -o-transform: rotate(-180deg);
    text-align: center;
    margin: 0;
}

.FilesWrapper {
    margin: auto;
    width: 210mm;
    display: grid;
    grid-gap: 40px;
    padding: 40px 0;
}

.File_wrapper {
    width: calc(210mm - 80px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    background: #fff;
    padding: 40px;
    box-shadow: 0 10px 29px 0 rgb(68 88 144 / 10%);
}

.File_wrapper h4 {
    align-self: flex-start;
}

.Image_container img {
    max-width: 100%;
}

.Docx_container {
    width: 100%;
}