.Report2_Wrapper .A4 {
    orientation: landscape;
    width: calc(297mm - 20mm);
    min-height: calc(210mm - 20mm);
    margin: auto;
    margin-top: 40px !important;
    box-shadow: 0 10px 29px 0 rgb(68 88 144 / 10%);
    background: #fff;
    padding: 10mm;
}


.Report2_Wrapper thead th {
    font-size: 12px;
}

.report2_announcement_row {
    background-color: #F4F9FF;
}

/* .report2_svod_row td,
.report2_svod_row th {
    border-left: 1px solid #eee;
} */

.report2_announcement_row th:nth-child(2) {
    text-align: left;
}

.report2_svod_row {
    font-family: Montserrat;
    cursor: default;
}

.report2_svod_row td a {
    color: #1c4fc3;
    padding: 10px 30px;
    cursor: pointer;
    width: 100%;
    height: 100%;
    text-decoration: none;
    transition: all 0.15s ease;
    -webkit-transition: all 0.15s ease;
    -moz-transition: all 0.15s ease;
    -ms-transition: all 0.15s ease;
    -o-transition: all 0.15s ease;
}

.report2_svod_row td a:hover {
    font-weight: 600;
    border-bottom: 1px solid #1c4fc3;
    transition: all 0.15s ease;
    -webkit-transition: all 0.15s ease;
    -moz-transition: all 0.15s ease;
    -ms-transition: all 0.15s ease;
    -o-transition: all 0.15s ease;
}

