
.Pdf_Document {
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
}

.PDF_Page {
    max-width: 100%;
}

.react-pdf__Page__canvas {
    width: 100% !important;
    height: auto !important;
}

.react-pdf__Page__textContent {
    display: none !important;
}

.control_panels {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: 30px;
    color: #75e0f3;
    justify-content: center;
    align-items: center;
    margin-bottom: 5px;
    background-color: #656565;
    border-radius: 5px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    padding: 10px;
}

.next_prev {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 7px;
    justify-content: center;
    align-items: center;
}

.control_zoom {
    display: grid;
    grid-template-columns: 20px 40px 20px;
    grid-gap: 3px;
    justify-content: center;
    align-items: center;
}

.download a {
    color: #ffffff;
    font-size: 16px;
    text-decoration: none;
    text-align: center;
}

.download i {
    margin-right: 10px;
    color: #75e0f3;
}

.control_panels span {
    color: #ffffff;
}

.clickable {
    cursor: pointer;
}

.disabled {
    cursor: not-allowed;
    color: #7e98a3;
}


@media screen and (max-width: 660px) {
    .control_panels {
        grid-template-columns: repeat(2, auto);
    }

    .Prikaz_container {
        padding: 15px;
    }

    .control_zoom {
        display: none;
    }
}