.Report1_Wrapper .Selectors_Wrapper {
    display: grid;
    grid-template-columns: auto 140px 170px 1fr 260px 40px;
    grid-gap: 20px;
}

.Selectors_Wrapper a {
    text-decoration: none;
}

.Report1_Wrapper h5 {
    margin: 10px;
    text-align: left;
}

@media print {
    .A4 {
        margin: 0 !important;
        padding: 10mm !important;
        box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.0);
    }
}

@page {
    size: A4;
    margin: 10mm;
}

@media only screen and (max-width: 980px) {
    .Report1_Wrapper .Selectors_Wrapper {
        grid-template-columns: auto 140px 170px 1fr;
    }
}